.header {
    width: 100%;
    display: flex;
}

.headerInner {
    margin: 0 auto;
    padding: 40px 28px;
    width: 100%;
    max-width: 1170px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.logo {
    font-family: "Mazzard", sans-serif;
    font-weight: 600;
    font-size: 25px;
    color: #fff;
}

.navigation {
    display: none;
}

.upLine {
    width: 32px;
    height: 2px;
    background-color: #fff;
    margin-bottom: 7px;
}

.underLine {
    width: 32px;
    height: 2px;
    background-color: #fff;
}